



























































import { Component, Prop, Vue } from 'vue-property-decorator'
import { deleteProperties, resetProperties } from '../assets/js/utils'

@Component({})
export default class File extends Vue {
  //   @Prop(String) id?: string;
  name = 'File'
  videoModel: any = {
    title: '',
    desc: '',
    author: '',
    cover: null,
    url: null,
    publish: false
  }
  image: any = null
  video: any = null
  rules = [value => !value || value.size < 2000000 || '封面图不超过 2 MB!']
  enable = false
  mounted() {}

  created() {
    const admin = window.localStorage.getItem('admin')
    if (admin == 'true') {
      this.enable = true
    }
  }
  async submit() {
    let formData = new FormData()
    if (this.video) {
      formData.append('video', this.video)
    }
    if (this.image) {
      formData.append('image', this.image)
    }
    // const { data } = await this.$http.post("uploadmany", formData);
    try {
      await this.$http
        .post('uploadmany', formData)
        .then(data => {
          this.videoModel.cover = data.data.cover
          this.videoModel.url = data.data.url
        })
        .then(async () => {
          const { data } = await this.$http.post('videos/create', this.videoModel)
          if (data.success) {
            this.$message.success('上传成功')
            this.reset()
          }
        })
    } catch (error) {
      this.$message.warning('上传失败')
    }
  }
  reset() {
    this.videoModel.title = ''
    this.videoModel.desc = ''
    this.videoModel.author = ''
    this.videoModel.cover = ''
    this.videoModel.url = ''
    this.videoModel.publish = false
    this.video = null
    this.image = null
  }
}
